import { setState } from "./setState"
import { getState } from "./getState"
import { isArray, isUndefined } from "lodash/fp"

import { createApi } from "../api"
import { getNewChildRowToState, getNewRowToState } from "./coreHandlers"

export const EVENT_TYPE_MEMBER_NAME = "##eventHandlerType"

export const eventHandlers = (store, coreApi, rootPath) => {
  const handler = (parameters, execute) => ({
    execute,
    parameters,
  })

  const setStateWithStore = (path, value) => setState(store, path, value)

  let currentState
  store.subscribe(s => {
    currentState = s
  })

  const api = createApi({
    rootPath: rootPath,
    setState: setStateWithStore,
    getState: (path, fallback) => getState(currentState, path, fallback),
  })

  const setStateHandler = ({ path, value }) => setState(store, path, value)

  return {
    "Set State": handler(["path", "value"], setStateHandler),
    "Load Row": handler(["rowKey", "statePath"], api.loadRow),
    "List Rows": handler(["indexKey", "statePath"], api.listRows),
    "Save Row": handler(["statePath"], api.saveRow),

    "Get New Child Row": handler(
      ["rowKey", "collectionName", "childRowType", "statePath"],
      getNewChildRowToState(coreApi, setStateWithStore)
    ),

    "Get New Row": handler(
      ["collectionKey", "childRowType", "statePath"],
      getNewRowToState(coreApi, setStateWithStore)
    ),

    Authenticate: handler(["username", "password"], api.authenticate),
  }
}

export const isEventType = prop =>
  isArray(prop) &&
  prop.length > 0 &&
  !isUndefined(prop[0][EVENT_TYPE_MEMBER_NAME])
