import { keyBy, mapValues } from "lodash/fp"
import { generate } from "shortid"
import {
  getNodeForCollectionPath,
  isSingleRow,
} from "../templateApi/hierarchy"
import { getNewFieldValue } from "../types"
import { $, joinKey, safeKey, apiWrapperSync, events } from "../common"
import { permission } from "../authApi/permissions"

export const getNew = app => (collectionKey, rowTypeName) => {
  const rowNode = getRowNode(app, collectionKey, rowTypeName)
  collectionKey = safeKey(collectionKey)
  return apiWrapperSync(
    app,
    events.rowApi.getNew,
    permission.createRow.isAuthorized(rowNode.nodeKey()),
    { collectionKey, rowTypeName },
    _getNew,
    rowNode,
    collectionKey
  )
}

export const _getNew = (rowNode, collectionKey) =>
  constructRow(rowNode, getNewFieldValue, collectionKey)

const getRowNode = (app, collectionKey) => {
  collectionKey = safeKey(collectionKey)
  return getNodeForCollectionPath(app.hierarchy)(collectionKey)
}

export const getNewChild = app => (rowKey, collectionName, rowTypeName) =>
  getNew(app)(joinKey(rowKey, collectionName), rowTypeName)

export const constructRow = (rowNode, getFieldValue, collectionKey) => {
  const row = $(rowNode.fields, [keyBy("name"), mapValues(getFieldValue)])

  row.id = `${rowNode.nodeId}-${generate()}`
  row.key = isSingleRow(rowNode)
    ? joinKey(collectionKey, rowNode.name)
    : joinKey(collectionKey, row.id)
  row.isNew = true
  row.type = rowNode.name
  return row
}
