import { ERROR } from "./standardState"

export const getNewChildRowToState = (coreApi, setState) => ({
  rowKey,
  collectionName,
  childRowType,
  statePath,
}) => {
  const error = errorHandler(setState)
  try {
    if (!rowKey) {
      error("getNewChild > rowKey not set")
      return
    }

    if (!collectionName) {
      error("getNewChild > collectionName not set")
      return
    }

    if (!childRowType) {
      error("getNewChild > childRowType not set")
      return
    }

    if (!statePath) {
      error("getNewChild > statePath not set")
      return
    }

    const rec = coreApi.rowApi.getNewChild(
      rowKey,
      collectionName,
      childRowType
    )
    setState(statePath, rec)
  } catch (e) {
    error(e.message)
  }
}

export const getNewRowToState = (coreApi, setState) => ({
  collectionKey,
  childRowType,
  statePath,
}) => {
  const error = errorHandler(setState)
  try {
    if (!collectionKey) {
      error("getNewChild > collectionKey not set")
      return
    }

    if (!childRowType) {
      error("getNewChild > childRowType not set")
      return
    }

    if (!statePath) {
      error("getNewChild > statePath not set")
      return
    }

    const rec = coreApi.rowApi.getNew(collectionKey, childRowType)
    setState(statePath, rec)
  } catch (e) {
    error(e.message)
  }
}

const errorHandler = setState => message => setState(ERROR, message)
